import React from 'react'
import {Card, Button, Col} from 'react-bootstrap'
import OneAlbum from './OneAlbum/OneAlbum.js'
import './Album.css'

class AlbumCard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            pop : false,
        }
    }

    setPopToggle = () => {
        this.setState({ pop : true })
    }

    onHide = () => {
        this.setState({ pop : false })
    }

    render() {
        const artistFont = (this.props.artistName.length > 30) ? "long-length" : "regular-length";
        const AlbumModal = (this.state.pop) ? <OneAlbum onHide={this.onHide} show={this.state.pop} albumName={this.props.albumName} artistName={this.props.artistName} songs={this.props.songs}/> : null;

        return(
            <Col md="4">
                {AlbumModal}
                <Card className="shadow rounded shadow-custom" style={{ width: '18rem', margin: '0 auto' }}>
                    <Card.Img variant="top" className="albumImgSize" src={this.props.albumPic} />
                    <Card.Body className="text-center">
                        <Card.Title >{this.props.albumName}</Card.Title>
                        <Card.Text className={artistFont} >
                            {this.props.artistName}
                        </Card.Text>
                        <Button onClick={this.setPopToggle} variant="outline-info" size="sm">View Album</Button>
                    </Card.Body>
                </Card>
            </Col>
            
        );
    }
}

export default AlbumCard;

