import React from 'react'

import { Modal } from 'react-bootstrap'


class OneAlbum extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            show: this.props.show,
        }
    }

    onHide = () => {
        this.props.onHide();
    }

    onShow = () => {
        this.setState({ show : true})
    }

    render() {
        const AllSongs = this.props.songs.map((song) => <li key={song}>{song}</li>);
        const header = `${this.props.albumName}` + " - " + `${this.props.artistName}`;
        return(
            <div>
                <Modal
                    size="lg"
                    show={this.state.show}
                    onHide={this.onHide}
                    aria-labelledby="example-modal-sizes-title-lg"
                >
                    <Modal.Header closeButton>
                    <Modal.Title id="example-modal-sizes-title-lg">
                        {header}
                    </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <ul>
                            {AllSongs}
                        </ul>
                    </Modal.Body>
                </Modal>
            </div>
        );
    }
}

export default OneAlbum;